<template>
  <div class="p2">
    <div class="content" v-show="p2show">
      {{bianliang}}
      <div class="cards" v-for="(item,index)  in cardsData" :key="index" :class="[index == 0?'backActive':'']">
        <div class="title">
          <span class="circle"></span>
          <span class="time">{{item.create_at | formData}}</span>
        </div>
        <div class="cardContent">
          <span ref="itemContent" :class="[isActive==index?'active':'']">{{item.content}}</span>
          <div class="seeMore" v-if="item.content.length > 107">

            <div v-if="index !==  isActive" @click="xianshi(index)">
              显示全部
              <img class="seeMoreIcon" src="../assets/img/seeMore.png" alt="">
            </div>
            <div v-else @click="shouqi(index)">
              收起
              <img class="roundSeeMoreIcon" src="../assets/img/seeMore.png" alt="">
            </div>
          </div>
        </div>

      </div>

    </div>
    <div class="daoDi" v-show="daoDiShow">
      已经到底了~
    </div>
  </div>
</template>
<script>
  var pageIndex = 1; //首页
  var isDaoDi = true;
  export default {
    name: 'p2',
    data() {
      return {
        data: '',
        name: 'p2',
        itemHeight: true,
        isActive: 'false',
        xianshiquanbu: '显示全部',
        isActive1: 'false',
        cardsData: [{
            create_at: '11:25',
            content: '9月26日波罗的海干散货指数上涨3.66%至1503点。已连涨6个交易日，涨幅达10.59%。此前，德银对亚太航运业近期发布报告称，称三大航线费率近期见升幅，近年船公司减低新运力供应相信持续多年的航太航运业近期发布报告相信持续多年的航太航运业近期发布报告9月26日波罗的海干散货指数上涨3.66%至1503点。已连涨6个交易日，涨幅达10.59%。此前，德银对亚太航运业近期发布报告称，称三大航线费率近期见升幅，近年船公司减低新运力供应相信持续多年的航太航运业近期发布报告相信持续多年的航太航运业近期发布报告'
          },
          {
            create_at: '10:55',
            content: '近年船公司减低新运力供应相信持续多年的航太航运业近期发布报告相信持续多年的航太航运业近期发布报告航运业近期发布报告相信持续多年的航太航运业近期发布报告'
          },
          {
            create_at: '8:25',
            content: '9月26日波罗的海干散货指数上涨3.66%至1503点。已连涨6个交易日，涨幅达10.59%。此前，德银对亚太航运业近期发布报告称，称三大航线费率近期见升幅，近年船公司减低新运力供应相信持续多年的航太航运业近期发布报告相信持续多年的航太航运业近期发布报告'
          }
        ],
        cardsDataHeight: [],
        contentStyle: {
          "display": "-webkit-box",
          "overflow": "hidden",
        },

        daoDiShow: false,
        bianliang: '',
        p2show:false,
      }
    },
    components: {

    },
    computed: {
      pid: function () {
        return this.$store.state.pid
      }
    },
    watch: {
      pid(val) {
        isDaoDi = true;
        
        this.daoDiShow  = false;
        this.loadList(val);
      }
    },
    methods: {
      xianshi(index) {
        this.isActive = index
      },
      shouqi(index) {
        this.isActive = 'fasle'
      },
      onScroll() {
        if (this.$route.name == 'p2') {
          var that = this;
          //可滚动容器的高度
          let innerHeight = document.querySelector('#app').clientHeight;
          //屏幕尺寸高度
          let outerHeight = document.documentElement.clientHeight;
          //可滚动容器超出当前窗口显示范围的高度
          let scrollTop = document.documentElement.scrollTop || document.body.scrollTop;

          //scrollTop在页面为滚动时为0，开始滚动后，慢慢增加，滚动到页面底部时，出现innerHeight < (outerHeight + scrollTop)的情况，严格来讲，是接近底部。
          if (innerHeight <= (outerHeight + scrollTop)) {

            //加载更多操作
            console.log("☕", isDaoDi)
              that.daoDiShow = true;
              pageIndex++; //加页数
              var that = this;
              var form = new FormData();
              console.log('⚽', pageIndex)
              form.append("model", "get_broadcast");
              form.append("page", pageIndex);
              form.append("offset", 5);
              form.append("product_id", this.pid);
              $.ajax({
                type: "POST",
                url: host + "/qi/page/index/index",
                data: form,
                mimeType: "text/html",
                processData: false,
                contentType: false,
                dataType: 'json',
                success: function (res) {
                  console.log(res)
                  res = res.data
                  if (res.length == 0) {
                    isDaoDi = false;
                    that.daoDiShow = true;
                  } else {
                    isDaoDi = true;
                    if (res.length > 0) {
                      for (let i = 0; i < res.length; i++) {
                        that.cardsData.push(res[i])
                      }
                      console.log(that.cardsData)
                    }
                    console.log(res.length)
                    //offset
                    if (res.length < 5) {
                      that.daoDiShow = true;
                    }
                  }


                }
              });
              this.items += 10;

            
          }
        }
      },
      loadList(val) {
        if (val !== '') {
          this.cardsData = [];
          pageIndex = 1;
          // var loading = this.$loading({
          //   lock: true,
          //   text: '',
          //   spinner: 'el-icon-loading',
          //   background: 'rgba(255, 255, 255，0.3)'
          // });
          console.log("loadmore");
          var that = this;
          var form = new FormData();
          form.append("model", "get_broadcast");
          form.append("page", pageIndex);
          form.append("offset", 5);
          form.append("product_id", val);

          $.ajax({
            type: "POST",
            url: host + "/qi/page/index/index",
            data: form,
            mimeType: "text/html",
            processData: false,
            contentType: false,
            dataType: 'json',
            success: function (res) {
         
              console.log(res)
              res = res.data
               if(res.length > 0){
                that.p2show = true;
              }
              that.cardsData = res
              window.addEventListener('scroll', that.onScroll);

            }
          });


        }
      }
    },
    activated() {
      window.removeEventListener('scroll', this.onScroll);
      isDaoDi = true;
      this.arrayClassOne = []
      //console.log('activated')
      this.loadList(this.pid);
      this.daoDiShow = false;
    },
    filters: {
      formData(time) {
        var time1 = time.toString().substring(11, 16)
        // console.log(time1)
        return time1
      }
    }
  }

</script>
<style scoped>
  .backActive .circle {
    border: .02rem solid #6686af;
  }

  .backActive .time {
    background: #6686af !important;
  }

  .backActive .cardContent {
    background: #f1f4fa !important;
  }

  .active {
    overflow: auto !important;
    display: inline !important;
  }

  .seeMore {
    text-align: right;
    color: #6686af;
    font-size: .25rem;
    height: .25rem;
    line-height: .25rem;
    margin-top: .13rem;
  }

  .roundSeeMoreIcon,
  .seeMoreIcon {
    float: right;
    width: .21rem;
    /* height: .21rem; */
    margin-left: .1rem;
  }

  .roundSeeMoreIcon {
    transform: rotate(180deg);
  }

  .p2 {
    margin-top: .25rem;
  }

  .content {
    background: white;
    padding-top: .45rem
  }

  .title {
    position: absolute;
    left: -0.08rem;
    top: -0.16rem;
    z-index: 0;
    /* height: .32rem; */
  }

  .circle {
    border-radius: 50%;
    border: .02rem solid #bdbdbd;
    width: .14rem;
    height: .14rem;
    float: left;
    margin-top: 0.09rem;
    background-color: #fff;
  }

  .time {
    float: left;
    width: .91rem;
    line-height : 1;
    padding-top: 0.05rem;
    padding-bottom: 0.05rem;
    /* height: .32rem; */
    /* vertical-align: middle; */
    text-align: center;
    background: #bdbdbd;
    border-radius: 10px;
    color: white;
    font-size: .25rem;
    margin-left: .21rem;
  }

  .cards {
    padding-top: .25rem;
    border-left: 1px solid #bdbdbd;
    padding-left: 0.3rem;
    padding-bottom: 0.4rem;
    margin-left: 0.28rem;
    position: relative;
    left: 0;
    top: 0;
    z-index: 0;
  }

  .cardContent {
    width: 6.5rem;
    font-size: .28rem;
    background-color: #f7f7f7;
    border-radius: 10px;
    margin: .12rem 0 0rem 0rem;
    padding: .3rem .25rem;
    text-align: left;
  }

  .cardContent span {
    max-height: 2.95rem;
    width: 6.0rem;
    word-wrap: break-word;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;

  }

</style>



